'use client';

import { useState, useEffect, useCallback } from 'react';
import { useRouter } from 'next/navigation';
import Link from 'next/link';
import {
  fetchPool,
  fetchPoolTransactions,
  formatCurrency,
  type PoolData,
  type PoolTransaction,
} from '@/lib/pool-api';

interface Agent {
  id: string;
  name: string;
  email: string;
  spinCredits: number;
}

type TransactionType = 'all' | 'bet' | 'payout' | 'deposit' | 'withdraw' | 'adjustment';

export default function PoolTransactionsPage() {
  const router = useRouter();
  const [agent, setAgent] = useState<Agent | null>(null);
  const [pool, setPool] = useState<PoolData | null>(null);
  const [transactions, setTransactions] = useState<PoolTransaction[]>([]);
  const [loading, setLoading] = useState(true);
  const [loadingMore, setLoadingMore] = useState(false);
  const [error, setError] = useState('');

  // Filters
  const [filterType, setFilterType] = useState<TransactionType>('all');
  const [filterStartDate, setFilterStartDate] = useState('');
  const [filterEndDate, setFilterEndDate] = useState('');
  
  // Pagination
  const [offset, setOffset] = useState(0);
  const [hasMore, setHasMore] = useState(true);
  const limit = 50;

  const loadTransactions = useCallback(async (reset = false) => {
    try {
      const currentOffset = reset ? 0 : offset;
      
      const params: {
        type?: string;
        startDate?: string;
        endDate?: string;
        limit: number;
        offset: number;
      } = {
        limit,
        offset: currentOffset,
      };

      if (filterType !== 'all') {
        params.type = filterType;
      }
      if (filterStartDate) {
        params.startDate = filterStartDate;
      }
      if (filterEndDate) {
        params.endDate = filterEndDate;
      }

      const result = await fetchPoolTransactions(params);
      
      if (result.success && result.data) {
        const txData = Array.isArray(result.data) ? result.data : [];
        if (reset) {
          setTransactions(txData);
        } else {
          setTransactions(prev => [...prev, ...txData]);
        }
        setHasMore(txData.length === limit);
        if (reset) {
          setOffset(limit);
        } else {
          setOffset(currentOffset + limit);
        }
      } else {
        if (reset) {
          setTransactions([]);
        }
      }
    } catch (err) {
      setError('Erro ao carregar transa√ß√µes');
      console.error(err);
    }
  }, [filterType, filterStartDate, filterEndDate, offset]);

  useEffect(() => {
    const token = localStorage.getItem('agentToken');
    const agentData = localStorage.getItem('agentData');

    if (!token || !agentData) {
      router.push('/');
      return;
    }

    setAgent(JSON.parse(agentData));
    
    // Load initial data
    const loadInitial = async () => {
      try {
        const poolResult = await fetchPool();
        if (poolResult.success && poolResult.data) {
          setPool(poolResult.data);
        }
        
        const txResult = await fetchPoolTransactions({ limit, offset: 0 });
        if (txResult.success && txResult.data) {
          const txData = Array.isArray(txResult.data) ? txResult.data : [];
          setTransactions(txData);
          setHasMore(txData.length === limit);
          setOffset(limit);
        } else {
          setTransactions([]);
        }
      } catch (err) {
        setError('Erro ao carregar dados');
        console.error(err);
        setTransactions([]);
      } finally {
        setLoading(false);
      }
    };

    loadInitial();
  }, [router]);

  // Recarregar quando filtros mudarem
  useEffect(() => {
    if (!loading && agent) {
      setOffset(0);
      loadTransactions(true);
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [filterType, filterStartDate, filterEndDate]);

  async function handleLoadMore() {
    setLoadingMore(true);
    await loadTransactions(false);
    setLoadingMore(false);
  }

  function getTransactionIcon(type: string): string {
    switch (type) {
      case 'bet': return 'üé∞';
      case 'payout': return 'üíµ';
      case 'deposit': return 'üì•';
      case 'withdraw': return 'üì§';
      case 'adjustment': return 'üîß';
      default: return 'üìã';
    }
  }

  function getTransactionLabel(type: string): string {
    switch (type) {
      case 'bet': return 'Aposta';
      case 'payout': return 'Pagamento';
      case 'deposit': return 'Dep√≥sito';
      case 'withdraw': return 'Saque';
      case 'adjustment': return 'Ajuste';
      default: return type;
    }
  }

  function getTransactionColor(type: string): string {
    switch (type) {
      case 'bet': return 'bg-blue-500/20 text-blue-400';
      case 'payout': return 'bg-red-500/20 text-red-400';
      case 'deposit': return 'bg-emerald-500/20 text-emerald-400';
      case 'withdraw': return 'bg-orange-500/20 text-orange-400';
      case 'adjustment': return 'bg-purple-500/20 text-purple-400';
      default: return 'bg-slate-500/20 text-slate-400';
    }
  }

  function handleLogout() {
    localStorage.removeItem('agentToken');
    localStorage.removeItem('agentData');
    router.push('/');
  }

  if (loading) {
    return (
      <div className="min-h-screen bg-linear-to-br from-slate-900 via-slate-800 to-slate-900 flex items-center justify-center">
        <div className="h-8 w-8 animate-spin rounded-full border-4 border-emerald-500 border-t-transparent" />
      </div>
    );
  }

  return (
    <div className="min-h-screen bg-linear-to-br from-slate-900 via-slate-800 to-slate-900">
      {/* Header */}
      <header className="border-b border-slate-700 bg-slate-800/80 backdrop-blur-sm sticky top-0 z-40">
        <div className="mx-auto max-w-6xl px-6 py-4">
          <div className="flex items-center justify-between">
            <div className="flex items-center gap-3">
              <div className="w-10 h-10 rounded-xl bg-linear-to-br from-emerald-500 to-blue-600 flex items-center justify-center">
                <span className="text-xl">üé∞</span>
              </div>
              <div>
                <h1 className="text-lg font-bold text-white">{agent?.name}</h1>
                <p className="text-xs text-slate-400">{agent?.email}</p>
              </div>
            </div>

            <div className="flex items-center gap-2 bg-slate-700/50 rounded-xl px-6 py-3 border border-slate-600">
              <span className="text-2xl">üè¶</span>
              <div className="text-right">
                <p className="text-xs text-slate-400">Saldo do Pool</p>
                <p className={`text-2xl font-bold ${(pool?.balance || 0) > 0 ? 'text-emerald-400' : 'text-red-400'}`}>
                  {formatCurrency(pool?.balance || 0)}
                </p>
              </div>
            </div>

            <button
              onClick={handleLogout}
              className="flex items-center gap-2 rounded-lg bg-slate-700 hover:bg-red-600/20 hover:text-red-400 border border-slate-600 hover:border-red-500/50 px-4 py-2 text-sm text-slate-300 transition"
            >
              <span>üö™</span>
              Sair
            </button>
          </div>
        </div>
      </header>

      {/* Navigation */}
      <nav className="border-b border-slate-700/50 bg-slate-800/30">
        <div className="mx-auto max-w-6xl px-6">
          <div className="flex gap-1">
            <Link href="/dashboard" className="px-4 py-3 text-sm font-medium text-slate-400 hover:text-white border-b-2 border-transparent hover:border-slate-600 transition">
              üìä Dashboard
            </Link>
            <Link href="/dashboard/pool" className="px-4 py-3 text-sm font-medium text-emerald-400 border-b-2 border-emerald-400">
              üè¶ Pool
            </Link>
            <Link href="/dashboard/games" className="px-4 py-3 text-sm font-medium text-slate-400 hover:text-white border-b-2 border-transparent hover:border-slate-600 transition">
              üéÆ Jogos
            </Link>
            <Link href="/dashboard/transactions" className="px-4 py-3 text-sm font-medium text-slate-400 hover:text-white border-b-2 border-transparent hover:border-slate-600 transition">
              üìã Transa√ß√µes
            </Link>
            <Link href="/dashboard/integration" className="px-4 py-3 text-sm font-medium text-slate-400 hover:text-white border-b-2 border-transparent hover:border-slate-600 transition">
              üîó Integra√ß√£o
            </Link>
            <Link href="/dashboard/settings" className="px-4 py-3 text-sm font-medium text-slate-400 hover:text-white border-b-2 border-transparent hover:border-slate-600 transition">
              ‚öôÔ∏è Configura√ß√µes
            </Link>
          </div>
        </div>
      </nav>

      {/* Breadcrumb */}
      <div className="mx-auto max-w-6xl px-6 py-4">
        <div className="flex items-center gap-2 text-sm">
          <Link href="/dashboard/pool" className="text-slate-400 hover:text-white transition">
            üè¶ Pool
          </Link>
          <span className="text-slate-600">/</span>
          <span className="text-white">Hist√≥rico de Transa√ß√µes</span>
        </div>
      </div>

      {/* Main Content */}
      <main className="mx-auto max-w-6xl px-6 pb-8">
        {error && (
          <div className="mb-6 p-4 rounded-lg bg-red-500/20 border border-red-500/50 text-red-300">
            {error}
          </div>
        )}

        {/* Filters */}
        <div className="rounded-xl border border-slate-700 bg-slate-800/50 p-4 mb-6">
          <div className="flex flex-wrap items-center gap-4">
            {/* Type Filter */}
            <div className="flex items-center gap-2">
              <span className="text-sm text-slate-400">Tipo:</span>
              <select
                value={filterType}
                onChange={(e) => setFilterType(e.target.value as TransactionType)}
                className="px-3 py-2 rounded-lg bg-slate-700 border border-slate-600 text-white text-sm focus:outline-none focus:border-emerald-500"
              >
                <option value="all">Todos</option>
                <option value="bet">Apostas</option>
                <option value="payout">Pagamentos</option>
                <option value="deposit">Dep√≥sitos</option>
                <option value="withdraw">Saques</option>
                <option value="adjustment">Ajustes</option>
              </select>
            </div>

            {/* Date Filters */}
            <div className="flex items-center gap-2">
              <span className="text-sm text-slate-400">De:</span>
              <input
                type="date"
                value={filterStartDate}
                onChange={(e) => setFilterStartDate(e.target.value)}
                className="px-3 py-2 rounded-lg bg-slate-700 border border-slate-600 text-white text-sm focus:outline-none focus:border-emerald-500"
              />
            </div>

            <div className="flex items-center gap-2">
              <span className="text-sm text-slate-400">At√©:</span>
              <input
                type="date"
                value={filterEndDate}
                onChange={(e) => setFilterEndDate(e.target.value)}
                className="px-3 py-2 rounded-lg bg-slate-700 border border-slate-600 text-white text-sm focus:outline-none focus:border-emerald-500"
              />
            </div>

            {/* Clear Filters */}
            {(filterType !== 'all' || filterStartDate || filterEndDate) && (
              <button
                onClick={() => {
                  setFilterType('all');
                  setFilterStartDate('');
                  setFilterEndDate('');
                }}
                className="text-sm text-slate-400 hover:text-white transition"
              >
                ‚úï Limpar filtros
              </button>
            )}
          </div>
        </div>

        {/* Transactions Table */}
        <div className="rounded-xl border border-slate-700 bg-slate-800/50 overflow-hidden">
          <div className="px-6 py-4 border-b border-slate-700">
            <h2 className="text-lg font-semibold text-white">üìã Transa√ß√µes do Pool</h2>
          </div>

          {transactions.length === 0 ? (
            <div className="p-12 text-center">
              <span className="text-4xl mb-4 block">üì≠</span>
              <p className="text-slate-400">Nenhuma transa√ß√£o encontrada</p>
              {(filterType !== 'all' || filterStartDate || filterEndDate) && (
                <p className="text-sm text-slate-500 mt-1">
                  Tente ajustar os filtros
                </p>
              )}
            </div>
          ) : (
            <>
              <div className="overflow-x-auto">
                <table className="w-full">
                  <thead>
                    <tr className="bg-slate-700/30">
                      <th className="px-6 py-3 text-left text-xs font-medium text-slate-400 uppercase">Data/Hora</th>
                      <th className="px-6 py-3 text-left text-xs font-medium text-slate-400 uppercase">Tipo</th>
                      <th className="px-6 py-3 text-right text-xs font-medium text-slate-400 uppercase">Valor</th>
                      <th className="px-6 py-3 text-right text-xs font-medium text-slate-400 uppercase">Saldo Antes</th>
                      <th className="px-6 py-3 text-right text-xs font-medium text-slate-400 uppercase">Saldo Ap√≥s</th>
                      <th className="px-6 py-3 text-left text-xs font-medium text-slate-400 uppercase">Detalhes</th>
                    </tr>
                  </thead>
                  <tbody className="divide-y divide-slate-700/50">
                    {(Array.isArray(transactions) ? transactions : []).map((tx) => (
                      <tr key={tx.id} className="hover:bg-slate-700/20 transition">
                        <td className="px-6 py-4 text-sm text-slate-300 whitespace-nowrap">
                          {new Date(tx.createdAt).toLocaleString('pt-BR')}
                        </td>
                        <td className="px-6 py-4">
                          <span className={`inline-flex items-center gap-1.5 px-2.5 py-1 rounded-full text-xs font-medium ${getTransactionColor(tx.type)}`}>
                            {getTransactionIcon(tx.type)} {getTransactionLabel(tx.type)}
                          </span>
                        </td>
                        <td className="px-6 py-4 text-right">
                          <span className={`font-semibold ${
                            tx.type === 'bet' || tx.type === 'deposit' ? 'text-emerald-400' : 'text-red-400'
                          }`}>
                            {tx.type === 'bet' || tx.type === 'deposit' ? '+' : '-'} {formatCurrency(Math.abs(tx.amount))}
                          </span>
                        </td>
                        <td className="px-6 py-4 text-right text-sm text-slate-400">
                          {formatCurrency(tx.balanceBefore)}
                        </td>
                        <td className="px-6 py-4 text-right text-sm text-slate-300 font-medium">
                          {formatCurrency(tx.balanceAfter)}
                        </td>
                        <td className="px-6 py-4 text-sm text-slate-400 max-w-xs truncate">
                          {tx.note && <span className="block">{tx.note}</span>}
                          {tx.gameCode && (
                            <span className="text-xs text-slate-500">
                              Jogo: {tx.gameCode}
                            </span>
                          )}
                          {tx.playerId && (
                            <span className="text-xs text-slate-500 block">
                              Player: {tx.playerId.substring(0, 8)}...
                            </span>
                          )}
                        </td>
                      </tr>
                    ))}
                  </tbody>
                </table>
              </div>

              {/* Load More */}
              {hasMore && (
                <div className="p-4 border-t border-slate-700 text-center">
                  <button
                    onClick={handleLoadMore}
                    disabled={loadingMore}
                    className="px-6 py-2 rounded-lg bg-slate-700 hover:bg-slate-600 text-white font-medium transition disabled:opacity-50"
                  >
                    {loadingMore ? (
                      <span className="flex items-center gap-2">
                        <span className="h-4 w-4 animate-spin rounded-full border-2 border-white border-t-transparent" />
                        Carregando...
                      </span>
                    ) : (
                      'Carregar mais'
                    )}
                  </button>
                </div>
              )}
            </>
          )}
        </div>

        {/* Summary Cards */}
        <div className="grid gap-4 sm:grid-cols-4 mt-6">
          <div className="rounded-lg border border-blue-500/30 bg-blue-500/10 p-4 text-center">
            <p className="text-sm text-blue-300">Apostas</p>
            <p className="text-xl font-bold text-blue-400">
              {(Array.isArray(transactions) ? transactions : []).filter(t => t.type === 'bet').length}
            </p>
          </div>
          <div className="rounded-lg border border-red-500/30 bg-red-500/10 p-4 text-center">
            <p className="text-sm text-red-300">Pagamentos</p>
            <p className="text-xl font-bold text-red-400">
              {(Array.isArray(transactions) ? transactions : []).filter(t => t.type === 'payout').length}
            </p>
          </div>
          <div className="rounded-lg border border-emerald-500/30 bg-emerald-500/10 p-4 text-center">
            <p className="text-sm text-emerald-300">Dep√≥sitos</p>
            <p className="text-xl font-bold text-emerald-400">
              {(Array.isArray(transactions) ? transactions : []).filter(t => t.type === 'deposit').length}
            </p>
          </div>
          <div className="rounded-lg border border-orange-500/30 bg-orange-500/10 p-4 text-center">
            <p className="text-sm text-orange-300">Saques</p>
            <p className="text-xl font-bold text-orange-400">
              {(Array.isArray(transactions) ? transactions : []).filter(t => t.type === 'withdraw').length}
            </p>
          </div>
        </div>
      </main>
    </div>
  );
}
